.slider-btn-container {
  width: 110px;
  height: 50px;
  /* border: 1px solid black; */
  background-color: rgb(130, 216, 130);
  border-radius: 0.5em;
  z-index: 1;
  overflow: hidden;
  box-shadow: 0px 0px 15px 2px rgba(0, 0, 0, 0.29);
}

.slider-btn {
  width: 110%;
  height: 100%;
  background-color: rgb(255, 255, 255);
  border-radius: 0.5em;
  transition: 0.2s ease-in-out;
}

.slider-btn:hover {
  background-color: rgb(236, 255, 236);
}

.btn-content {
  position: relative;
  transform: translateY(-52px);
  left: 50%;
  z-index: -1;
  font-weight: 800;
  color: rgb(43, 71, 43);
}
